.portfolio {
  height: 600vh;
  position: relative;
  scrollbar-width: 0;
}

.pList {
  position: sticky;
  top: 0;
  display: flex;
  height: 100vh;
  width: max-content;
}

.pItem {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 100px;
}

